body {
  background-image: url(./img/menu01.jpg);
  background-size: cover;
  width: 100%;
    height: 100vh;
    background-position: center;
}
.main{
  width: 100%;

display: flex;
text-align: center;
color: white;
font-family:Pretendard;
}
.menu{
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}
.title > p {
  font-weight: bold;
  font-size: 9rem;
  margin: 2rem;
  white-space: nowrap;
}
.menu_01 {
  display: flex;
  flex-direction: column;
}
.menu_01_text{
  font-weight: bold;
  margin-top: 2.5rem;
  font-size: 3rem;
}

@media only screen and (max-width: 786px) {
  .title{
  margin-top: 2rem;
  margin-bottom: 2rem;
  }
  .title > p{
    margin: 0;
    font-size: 2rem;
  }
  .menu_01_text{
    margin-top: 2.5rem;
    font-size: 1.5rem;
    font-weight: normal;
  }
}